#header {
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 0;
  /* z-index: 1; */
  background-repeat: no-repeat;
  background-size: cover;
  overflow-x: hidden;
}
.s1overlay,
.s5overlay {
  position: absolute;
