#usluge header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#usluge header h2 {
  font-size: 3rem;
  font-family: 'Raleway';
  text-transform: uppercase;
  color: lightslategray;
  letter-spacing: 0.1rem;
}

#usluge header h3 {
  font-weight: 400;
  font-style: italic;
  font-size: 1.8rem;
  margin-top: 0.5rem;
  color: #666;
  font-family: 'Playfair Display', Arial, Helvetica, sans-serif;
  position: relative;
}

.lineh {
  margin-top: 1rem;
  width: 0.1rem;
  height: 5rem;
  background-color: #ccc;
}

.linec {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  border: 0.2rem solid #70bad2;
}

#uslugebody {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 5rem;
  gap: 5rem;
  padding-top: 5rem;
  border-top: 1px solid rgba(83, 109, 254, 0.1);
  margin-top: 2rem;
}

#uslugebody .usluge {
  display: grid;
  grid-template-rows: auto auto 1fr;
  justify-items: center;
  text-align: center;
  position: relative;
}
#uslugebody .usluge .imgwrapper {
  width: 150px;
  height: 150px;
  background-color: #eff2f7;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #eff2f7;
  position: relative;
  margin-bottom: 2.5rem;
}

#uslugebody .usluge .imgwrapper::before {
  content: '';
  position: absolute;
  width: 120%;
  height: 120%;
  background-color: transparent;
  border-radius: 50%;
  overflow-x: hidden;
  border: 2px solid #eff2f7;
}

#uslugebody .usluge h3 {
  margin-top: 2rem;
  font-size: 2.5rem;
  color: #333;
  font-family: 'Raleway', Arial, Helvetica, sans-serif;
  position: relative;
}

#uslugebody .usluge h3::before {
  position: absolute;
  content: '';
  width: 2.5rem;
  height: 0.2rem;
  bottom: -1rem;
  left: 50%;
  transform: translateX(-50%);
  background-color: #536dfe;
}
#uslugebody .usluge p {
  margin-top: 2rem;
  font-size: 1.3rem;
  line-height: 2.6rem;
  font-family: 'Raleway';
  color: #333;
  text-align: left;
}
#uslugebody .usluge img {
  position: absolute;
  transform: translateY(-0.5rem);
  background-color: #eff2f7;
  width: 100px;
}
.usluge i {
  color: rgba(173, 216, 230, 0.4);
}
