/* LOOK AROUND SECTION */

#lookaround {
  margin-top: 10rem;
  height: 100vh;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  background: url(/elektro_plus/static/media/installationsidebar.bdef8041.jpg) center/cover;
}

#lookaroundimg {
  background: url('/img/installationsidebar.jpg') no-repeat / cover;
}

#lookaroundtxt {
  padding: 1rem 2rem 0 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: left;
  font-size: 1.4rem;

  color: #fff;
  background-color: rgba(0, 0, 0, 0.3);
}

#lookaroundtxt h3 {
  margin-bottom: 2rem;
}
#lookaroundtxt p {
  line-height: 2.5rem;
}
#lookaroundtxt p:last-of-type {
  margin-top: 1rem;
}

#lookaroundtxt a {
  margin-top: 2rem;
  color: #fff;
  text-decoration: none;
}

/* PROJECTS */

#projects {
  margin-top: 10vh;
  width: 100vw !important;
  height: 100vh;
  margin-left: -5vw;
  position: relative;
}

#projects::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(/elektro_plus/static/media/showcaseknx1.977a9ef1.jpg) no-repeat top center /
    cover;
  z-index: -1;
}
#projects::before {
  content: '';
  position: absolute;
  top: -10%;
  left: 0;
  width: 100%;
  height: 15%;
  background: #fff;
  z-index: 2;
  transform: skewY(-2deg);
}
