#projects #project {
  position: relative;
  max-width: 77vw;
  height: 35rem;
  padding: 0 auto;
  /* background-color: rgba(0, 0, 0, 0.5); */
  /* bottom: calc(0px - (35rem / 2)); */

  top: 75%;
  left: 50%;
  transform: translateX(-50%);
  overflow: hidden;
  /* background-color: red; */
}

#projects #prev {
  position: absolute;
  bottom: -5%;
  left: 5%;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.7);
  cursor: pointer;

  border-radius: 4px;
  padding: 2rem 1rem;
  transition: background-color 0.4s ease;
}
#projects #next {
  cursor: pointer;

  position: absolute;
  bottom: -5%;
  right: 5%;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 4px;
  padding: 2rem 1rem;
  transition: background-color 0.4s ease;
}

#projects #prev i {
  color: #fff;
}
#projects #next i {
  color: #fff;
}

#projects #project #projectslider {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  width: 100%;
  height: 100%;
  position: relative;
  /* padding-left: 1.7vw; */
  transform: translateX(0vw);
  transition: transform 0.5s ease;
}

#projects #project #projectslider #one {
  width: 24vw;
  height: 90%;
  background-color: red;
  background: url(/elektro_plus/static/media/jakastruja.6033c3d8.jpg) no-repeat center/cover;
  position: relative;
  border-top: 5px solid #536dfe;
  left: 0;
}
.project h3 {
  position: absolute;
  font-size: 2rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  text-transform: uppercase;
}
#projects #project #one::before {
  transition: background-color 0.5s ease;
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
#projects #project #one:hover::before,
#projects #project #two:hover::before,
#projects #project #three:hover::before,
#projects #project #four:hover::before,
#projects #project #five:hover::before,
#projects #project #six:hover::before {
  background-color: rgba(0, 0, 0, 0);
}
#projects #project #two::before {
  transition: background-color 0.5s ease;
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
#projects #project #three::before,
#projects #project #four::before,
#projects #project #five::before,
#projects #project #six::before {
